import common from "../../common/common.js";
export default {
	data() {
		return {
			tempFace:""
		};
	},
	onLoad(params) {
		console.log(params)
		var tempFilePaths = params.tempFilePaths;
		this.tempFace = tempFilePaths;
	},
	methods: {
		changePendingFace(){
			var me = this;
			uni.chooseImage({
				count:1,
				sizeType:["compressed"],
				sourceType:["album"],
				success(res) {
					//获得临时路径
					var tempFilePaths = res.tempFilePaths[0];
					me.tempFace = tempFilePaths
				}
			})
		},
		upload(){
			var me = this;
			var globalUser = me.getGlobalUser("globalUser");
			
			uni.showLoading({
				mask:true,
				title:"上传中,请稍后..."
			})
			uni.uploadFile({
				url:common.serverUrl + `/user/uploadFace?userId=${globalUser.id}&qq=${me.qq}`,
				filePath:me.tempFace,
				name:"file",
				header:{
					headerUserId: globalUser.id,
					headerUserToken: globalUser.userUniqueToken
				}, 
				success(res) {
					var resData = JSON.parse(res.data) ;
					console.log(resData)
					if(resData.status == 200){
						//获得最新的用户数据
						var userInfo = resData.data;
						uni.setStorageSync("globalUser",userInfo);
						uni.navigateBack({
							delta:1
						})
					}else if(resData.status == 502 || resData.status == 500){
						uni.showToast({
							title:resData.msg,
							image:"/static/icos/error.png",
							duration:2000
						})
					}
				},
				complete() {
					uni.hideLoading()
				}
			})
			
		}
	}
}
